[class*="__container"] {
    max-width: 1200px;
    margin: 0 auto;
    box-sizing: border-box;
}

.HomePage {
}
.home__container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.HomePageBlock {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    font-size: 28px;
    line-height: 42px;
    font-style: italic;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}
h1 {
    display: flex;
    justify-content: center;
    text-align: center;
    text-shadow: 10px 10px 10px #927c7c;
    color: #605151;
    font-weight: 800;
    font-size: 34px;
    font-style: italic;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}

.paragraph {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: justify;
    flex-wrap: wrap;
    font-weight: 500;
    font-style: italic;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    box-sizing: border-box;
    padding: 0px 20px;
}
.imgHome {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-bottom: 10%; 
}

.imgHome img {
    padding: 30px;
    width: 500px;
    border: 4px solid #dddddda2;
}

@media (max-width: 769px) {
    .HomePage {
        font-size: 22px;
        line-height: 26px;
    }
h1 {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 22px;
    line-height: 24px;
}

.paragraph {
    display: flex;
    justify-content: center;
    font-size: 16px;
    line-height: 22px;
    padding: 0px 40px;
}
.imgHome {
    width: 100vw;
    display: flex;
    justify-content: center;
    padding-bottom: 10%;
    flex-wrap: wrap;
}

.imgHome img {
    padding: 20px;
    width: 400px;
    min-width: 40%;
}
}
